@import './variables.scss';
@import './body.scss', './aboutMe.scss', './media.scss', './header.scss';

.content{
    &__part{
        display: flex;
        justify-content: space-between;
    }
}
.article{
    width: 40%;
    display: flex;
    align-items: flex-start;
    justify-content: center;
    flex-direction: column;
    &__button {
        background-color: $buttonColor;
        border: none;
        color: $textColor;
        width: 50%;
        text-align: center;
        text-decoration: none;
        font-size: 20px;
        cursor: pointer;
    }
}



.personal-image {
    width: 60%;
    justify-content: flex-end;
    @include flexAICenter();
    &__image {
        width: 55%;
    }
}

.text-values {
    width: 100%;
    margin: 0px 10px 0px 10px;

    h3,
    p {
        width: 90%;
    }
}
.work-section{
    &__showcase {
        width: 100%;
        display: flex;
        justify-content: space-between;
    }
}
.project{
    background-color: $buttonColor;
    width: 31%;
    aspect-ratio: 1/1;
    &__link{
        text-decoration: none;
        &:visited {
            color: $textColor;
        }
    }
    &__image {
        height: 60%;
        width: 100%;
    }

}

   