@import './variables.scss';

@media only screen and (max-width:768px) {
    nav {
        &__list {
            width: 100%;
            justify-content: center;
            padding: 0px;
        }
    }

    .content {
        width: 80%;
    }

    .article {
        flex-direction: column;

        &__paragraph {
            width: 100%;

        }

        &__button {
            width: 100%;
        }

    }

    .personal-image {
        display: none;
    }

    .work-section {
        &__showcase {
            flex-direction: column;
        }
    }

    .project {
        aspect-ratio: auto;
        width: 100%;
        margin-bottom: 8%;
    }

    .content {
        &__table {
            width: 100%;
        }
    }


}
